@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
* {
    font-family: 'Pangolin', cursive;
}

body {
    overflow-x: hidden;
}

body,
div {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    margin-top: 10px;
}
.section1{
    width: 100%;
    max-width: 100%;
    height: 100%;
    position: relative;
    top: 990;
    /* z-index: -9; */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: black;
}

.section2{
    width: 100vw;
    max-width: 100%;
    height: 550px;
    position: relative;
    top: 100%; 
    /* z-index: 99; */
    background-color:#051d21;
}

.section3{
    width: 100vw;
    max-width: 100%;
    height: 550px;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(245,168,27);
    font-size: 20px;
    color: #f7f7f2;
    padding-top: 15vh;
    
}
.sky{
    position: fixed;
    height: 100%;
    top: 0%;
}

.clouds{
    width: 85%;
    height: auto;
    /* position: fixed; */
    position: fixed;
    top: 3%;
    left: 11%;
}

.front{
    width: 45%;
    height: auto;
    /* position: fixed; */
    position: fixed;
    top: 45%;
    left: 25%;
}

.buildings{
    width : 55%;
    height: auto;
    position: absolute;
    top: 35%;
    left: 15%;
}

.building{
    width: 100%;
    height: auto;
    position: relative;
}
.plane3{
    width: 50%;
    max-width: 30%;
    height: auto;
    position: fixed;
    /* position: absolute; */
    padding-top: 35%;
    right: 38%;
}

.svg_curve_top{
    width: 100%;
    height:5vw;
    position: absolute;
    -webkit-transform: translateY(-99%);
            transform: translateY(-99%);
}

.svg_curve_bottom{
    width: 100%;
    height:5vw;
    position: absolute;
}

.section2_container{
    padding: 30px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.left_side{
    width: 50%;
    height: 100%;
    display:inline-table;
    padding: 45px;
    justify-content:center;
    align-items: center;
    color: white;
 
}
.right_side{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content:center;
    align-items: center;

}
.image{
    width: 30vw;
    height: auto;
    border-radius: 50%;
    box-shadow: 0px 0px 3px 5px #f7f7f2;

}

.input_email{
    margin: 10px 0;
    padding: 10px;
    width: 60%;
    height: 60px;
    border: 1px solid #f7f7f2;
    border-radius: 3px;
    outline: none;
    font-size: 25px;
}

.contacts{
    padding: top;
    margin-top: 110px;
    width: 100%;
    height: 30%;
    display: flex;
    justify-content:space-evenly;
    background-color: #dc7c10;    

}

@media only screen and (max-width: 501px) {
    .plane3 {
        max-width: 40%;
    }

    .section2_container {
        flex-direction: column;
    }

    .left_side {
        width: 100%;
        height: 50%;
    }

    .right_side {
        width: 100%;
        height: 50%;
    }

    .image {
        width: auto;
        height: 100%;
    }

    .contact_section {
        margin: 0 10px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        font-size: 17px;
    }
}

a{
    text-decoration: none;
    color:white;
}
.button_container{
    margin-top: 10px;
    position: relative;
    display:flex;
    justify-content: center;
    align-items: center;
}

.my_button{
    width: 200px;
    height: 50px;
    border-radius: 3px;
    display:flex;
    justify-content: center;
    align-items: center;
    

    background-color: #dc7c10;
    cursor: pointer;
}

.button_image{
    width: 10px;
    height: 10px;
    position:absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    justify-content: center;
    align-items: center;
}

.clicked {
    -webkit-animation-name: clicked;
            animation-name: clicked;
    -webkit-animation-duration: .2s;
            animation-duration: .2s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes clicked {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    10% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    20% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@keyframes clicked {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    10% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    20% {
        -webkit-transform: scale(1.3);
                transform: scale(1.3);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

.launch_plane {
    -webkit-animation-name: launch_plane;
            animation-name: launch_plane;
    -webkit-animation-duration: 1.7s;
            animation-duration: 1.7s;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}

@-webkit-keyframes launch_plane {
    0% {
        -webkit-transform: scale(0) translate(0%, 0%) rotate(0);
                transform: scale(0) translate(0%, 0%) rotate(0);
    }
    5% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    10% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(1) translate(0%, 0%) rotate(0);
                transform: scale(1) translate(0%, 0%) rotate(0);
    }
    100% {
        -webkit-transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
                transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
    }
}

@keyframes launch_plane {
    0% {
        -webkit-transform: scale(0) translate(0%, 0%) rotate(0);
                transform: scale(0) translate(0%, 0%) rotate(0);
    }
    5% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    10% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
    20% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    40% {
        -webkit-transform: scale(1) translate(0%, 0%) rotate(0);
                transform: scale(1) translate(0%, 0%) rotate(0);
    }
    100% {
        -webkit-transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
                transform: scale(1) translate(150vw, -190vh) rotate(-30deg);
    }
}
